import ActivityInfoDrawer from '@/components/Activity/ActivityInfoDrawer';
import Pagination from '@/components/Pagination';
import { applicationHistory } from '@/api/activityApplication';
var columns = [{
  title: 'ID',
  dataIndex: 'id',
  sorter: true
}, {
  title: '活动名称',
  dataIndex: 'activity.name'
}, {
  title: '申请理由',
  key: 'reason',
  dataIndex: 'reason',
  scopedSlots: {
    customRender: 'reason'
  }
}, {
  title: '审批备注',
  dataIndex: 'remark',
  scopedSlots: {
    customRender: 'remark'
  }
}, {
  title: '申请状态',
  dataIndex: 'status',
  scopedSlots: {
    customRender: 'status'
  }
}, {
  title: '申请日期',
  dataIndex: 'created_at'
}, {
  title: '审批日期',
  dataIndex: 'confirmed_time',
  scopedSlots: {
    customRender: 'confirmed_time'
  }
}, {
  title: '审批人',
  dataIndex: 'confirmed_user',
  scopedSlots: {
    customRender: 'confirmed_user'
  }
}, {
  title: '操作',
  scopedSlots: {
    customRender: 'operation'
  }
}];
export default {
  components: {
    Pagination: Pagination,
    ActivityInfoDrawer: ActivityInfoDrawer
  },
  filters: {
    objectNull: function objectNull(val) {
      if (val == null) {
        return '暂无此数据';
      } else {
        return val.real_name;
      }
    },
    noNull: function noNull(val) {
      if (val == null) {
        return '暂无此数据';
      } else {
        return val;
      }
    },
    statusToText: function statusToText(val) {
      switch (val) {
        case 0:
          return '申请中';

        case 1:
          return '审批通过';

        case 2:
          return '审批不通过';
      }
    }
  },
  data: function data() {
    return {
      data: [],
      loading: false,
      columns: columns,
      paginationData: {},
      infoDrawerVisible: false,
      aid: undefined
    };
  },
  created: function created() {
    this.init();
  },
  methods: {
    init: function init() {
      this.getList();
    },
    getList: function getList() {
      var _this = this;

      applicationHistory().then(function (res) {
        console.log(res);
        _this.data = res.data.data;
        _this.paginationData = res.data;
      });
    },
    handlePageChange: function handlePageChange(url) {},
    handleInfoBtnClick: function handleInfoBtnClick(activityId) {
      this.aid = activityId;
      this.infoDrawerVisible = true;
    }
  }
};